<template>
    <div style="height:200px;overflow-y: auto;">
      <!--<h3 style="height:40px;line-height:40px;">心电图列表</h3>-->
      <v-table
        is-horizontal-resize
        is-vertical-resize
        column-width-drag
        style="width:100%;color:#666;"
        :height=height
        :title-row-height=38
        :row-height=34
        :columns="patientColumns"
        :table-data="patientEcgData"
        row-hover-color="#fcf0bf"
        row-click-color="#fbf8ea"
        title-bg-color="#a6b7de"
        even-bg-color="#f3f5fb"
        :row-click="rowClick"
        :paging-index="(pageIndex-1)*pageSize"
      ></v-table>
      <!--<div class="mt20 mb20 bold" style="margin-top: 20px;text-align: center">-->
        <!--<v-pagination ref="pagea"-->
                      <!--@page-change="pageChange"-->
                      <!--@page-size-change="pageSizeChange"-->
                      <!--:total="total"-->
                      <!--:page-size="pageSize"-->
                      <!--:layout="['total', 'prev', 'pager', 'next', 'sizer', 'jumper']">-->
        <!--</v-pagination>-->
    <!--</div>-->
    </div>
</template>

<script>
    export default {
        name: "view-ecg-list",
      data(){
          return {
            height:300,
            pageIndex: 1,//当前页
            pageSize: 30,//一页多少条数据
            total: 0,    //总条数
            patientColumns:[
              {
                field: 'custome', title:'心电图序号', width: 50, titleAlign: 'center', columnAlign: 'center',
                formatter: function (rowData,rowIndex,pagingIndex,field) {
                  return rowIndex < 3 ? '<span style="color:red;font-weight: bold;">' + (rowIndex + 1) + '</span>' : rowIndex + 1
                }, isFrozen: true,isResize:true
              },
              {field: 'ecg_name', title: '心电图名称', width: 50, titleAlign: 'center', columnAlign: 'center',isResize:true , orderBy: ""},
              {field: 'creation_time', title: '创建时间', width: 50, titleAlign: 'center', columnAlign: 'center',isResize:true,overflowTitle:true},
            ],
            patientEcgData:[]
          }
      },
      props:{
        patient:{type:Object,default:{}}
      },
      create(){},
      mounted(){

      },
      methods:{
          init(){
            var pThis=this;
            pThis.$http.post(pThis.host("patecg/getPatecg"),{pk_pat_info:pThis.patient.pk_pat_info})
              .then(function (response) {
                let datajson = response.data;
                let backjson={"state":datajson.state,"msg":datajson.msg};
                pThis.backLogin(backjson);
                if (datajson.state == "1") {
                  pThis.patientEcgData=datajson.results;
                }
              })
              .catch(function (error) {
                console.log(error);
              });
          },
        rowClick(rowIndex, rowData, column){
            var pThis=this;
          // patientName patientSex patientAge seriesData
          // console.log(pThis.patient)
          // if(pThis.patient.name && pThis.patient.gender && patient.age_value){
            window.open("scheme2://host2:8080/path2?seriesInstanceUID="+rowData.pk_pat_ecg+"&patientName="+pThis.patient.name+"&patientSex="+pThis.patient.gender+"&patientAge="+pThis.patient.age_value+"&seriesData="+rowData.creation_time);
          // }
          //   window.open("scheme2://host2:8080/path2?seriesInstanceUID="+rowData.pk_pat_ecg);


        }
      }
    }
</script>

<style scoped>

</style>



