              <li class="borderRadius0">
                <label>实时监控盒子编号</label>
                <Select :disabled="util.disabledFlag" style="width:200px" v-model="patientinfo.padPatInfoView.box_code">
                  <Option v-for="item in deviceCodeArr" :value="item.code" :key="item.pk_defdoc">{{ item.name }}</Option>
                </Select>
              </li>
            </ul>
            <!--tab-->
            <ul class="versionNav">
              <li @click="versionNavChange($event,0)">
                <h3 class="modelTitle modelTitleActive" style="border-bottom:none;">心电图</h3>
              </li>
              <li style="width:1px;height:16px;background:#dcdcdc;padding:0;margin-top:14px;"></li>
              <li @click="versionNavChange($event,1)">
                <h3 class="modelTitle" style="border-bottom:none;">生命体征</h3>
              </li>
              <li style="width:1px;height:16px;background:#dcdcdc;padding:0;margin-top:14px;"></li>
              <li @click="versionNavChange($event,2)" style="border-bottom:none;">
                <h3 class="modelTitle">辅助检查</h3>
              </li>
              <ul class="heartShare">
                <li style="position:relative;" v-if="!ecg" @click="collectEcg" >获取心电图Id</li>
                <li style="position:relative;" v-if="ecg"><a style="display:block;" id="aa" :href="src">采集心电图</a></li>
                <li style="position:relative;" @click="viewEcg">查看心电图</li>
                <li @click="weixinShare">微信分享</li>
              </ul>
            </ul>
            <div class="versionContent">
              <div class="changeContent">
                <ul class="xindiantu clearfix ulOne">
                  <li>
                    <FormItem label="院前首份心电图"  class="formItem borderRadius0">
                      <DatePicker :transfer="true" :editable = "true" :readonly="util.disabledFlag" type="datetime" class="date-input" style="width:200px;"
                                  @on-open-change="scrollChange(3)" @on-change="dateChange($event,['patientinfo','padTimeView','phep_ecg_time'],19)"
                      v-model="patientinfo.padCheckInfoView.phep_ecg_time" format="yyyy-MM-dd HH:mm"></DatePicker>
                    </FormItem>
                  </li>
                  <li >
                    <FormItem label="首份心电图确诊时间"  class="formItem borderRadius0">
                      <DatePicker :transfer="true" :editable = "true" :readonly="util.disabledFlag" type="datetime" class="date-input" style="width:200px;"
                                  @on-open-change="scrollChange(3)" @on-change="dateChange($event,['patientinfo','padTimeView','ecg_diagnose_time'],19)"
                                  v-model="patientinfo.padTimeView.ecg_diagnose_time" format="yyyy-MM-dd HH:mm"></DatePicker>
                    </FormItem>
                  </li>
                  <li>
                    <FormItem label="传输时间"  class="formItem borderRadius0">
                      <DatePicker :transfer="true" :editable = "true" :readonly="util.disabledFlag" type="datetime" class="date-input" style="width:200px;"
                                  @on-open-change="scrollChange(4)" @on-change="dateChange($event,['patientinfo','padTimeView','tran_date'],19)"
                                  v-model="patientinfo.padTimeView.tran_date" format="yyyy-MM-dd HH:mm"></DatePicker>
                    </FormItem>
                  </li>
                  <li  >
                    <FormItem label="传输方式"  class="formItem borderRadius0">
                      <Select :disabled="util.disabledFlag" placement="top" style="width:200px;text-align:left;display:inline-block;" placeholder="" v-model="patientinfo.padCheckInfoView.is_remote_ecgtran">
                        <Option v-for="item in transArr" :value="item.code" :key="item.pk_defdoc">{{ item.name }}</Option>
                      </Select>
                    </FormItem>
                  </li>
                  <li  style="width:100%;margin-top:10px;">
                    <FormItem label="心电图诊断结论" prop="ecg_conclusion" class="formItem borderRadius0 textareaTip">
                      <Input  @on-focus="scrollChange(6)"
                              :readonly="util.disabledFlag" v-model="patientinfo.padCheckInfoView.ecg_conclusion"
                              class="checkTextarea" type="textarea"  placeholder=""></Input>
                    </FormItem>
                  </li>
                </ul>
                <ul class="xindiantu clearfix">
                  <li >
                    <FormItem label="意识" class="formItem borderRadius0">
                      <!--<Input :readonly="util.disabledFlag" style="width:200px;" v-model="patientinfo.padCheckInfoView.consciousness_type"></Input>-->
                      <Select placement="top" :disabled="util.disabledFlag" style="width:200px;" placeholder="" v-model="patientinfo.padCheckInfoView.consciousness_type">
                        <Option v-for="item in awareArr" :value="item.code" :key="item.pk_defdoc">{{ item.name }}</Option>
                      </Select>
                    </FormItem>
                  </li>
                  <li>
                    <FormItem label="呼吸" prop="respiration_rate" class="formItem borderRadius0 checkError">
                      <!--<Input :readonly="util.disabledFlag" style="width:130px;" :number="true" :maxlength="3" @on-keyup="bloodRule($event,['respiration_rate'])" v-model="patientinfo.padCheckInfoView.respiration_rate"></Input>-->
                      <input @focus="scrollChange(3)" :readonly="util.disabledFlag" class="nativeInput" type="number" style="width:130px;"  maxlength="3"  @keyup="bloodRule($event,['respiration_rate'],3)" v-model="patientinfo.padCheckInfoView.respiration_rate"/>
                      <span>次/分钟</span>
                    </FormItem>
                  </li>
                  <li>
                    <FormItem label="脉搏" prop="pulse_rate" class="formItem borderRadius0 checkError">
                      <!--<Input :readonly="util.disabledFlag" style="width:130px;" :number="true" :maxlength="3" @on-keyup="bloodRule($event,['pulse_rate'])"  v-model="patientinfo.padCheckInfoView.pulse_rate"></Input>-->
                      <input @focus="scrollChange(4)" :readonly="util.disabledFlag" class="nativeInput" type="number" style="width:130px;"  maxlength="3"  @keyup="bloodRule($event,['pulse_rate'],3)" v-model="patientinfo.padCheckInfoView.pulse_rate"/>
                      <span>次/分钟</span>
                    </FormItem>
                  </li>
                  <li >
                    <FormItem label="心率" prop="heart_rate" class="formItem borderRadius0 checkError">
                      <!--<Input :readonly="util.disabledFlag" style="width:130px;" :number="true" :maxlength="3" @on-keyup="bloodRule($event,['heart_rate'])" v-model="patientinfo.padCheckInfoView.heart_rate"></Input>-->
                      <input @focus="scrollChange(4)" :readonly="util.disabledFlag" class="nativeInput" type="number" style="width:130px;"  maxlength="3"  @keyup="bloodRule($event,['heart_rate'],3)" v-model="patientinfo.padCheckInfoView.heart_rate"/>
                      <span>次/分钟</span>
                    </FormItem>
                  </li>
                  <li >
                    <FormItem label="血压"  class="formItem borderRadius0 checkError">
                      <input @focus="scrollChange(5)" :readonly="util.disabledFlag" class="nativeInput" type="number" style="width:55px;"
                             maxlength="3" @blur="bloodBlur(patientinfo.padCheckInfoView.blood_pressure_low,patientinfo.padCheckInfoView.blood_pressure_high)"
                             @keyup="bloodRule($event,['blood_pressure_high'],3)" v-model="patientinfo.padCheckInfoView.blood_pressure_high"/>
                      <span style="width:10px;text-align:center;">/</span>
                      <input @focus="scrollChange(5)" :readonly="util.disabledFlag" class="nativeInput" type="number" style="width:55px;"
                             maxlength="3"  @blur="bloodBlur(patientinfo.padCheckInfoView.blood_pressure_low,patientinfo.padCheckInfoView.blood_pressure_high)"
                             @keyup="bloodRule($event,['blood_pressure_low'],3)" v-model="patientinfo.padCheckInfoView.blood_pressure_low"/>
                      <span>mmHg</span>
                    </FormItem>
                  </li>
                  <li  >
                    <FormItem label="Killip分级"  class="formItem borderRadius0">
                      <Select :disabled="util.disabledFlag" style="width:200px;text-align:left;display:inline-block;" placeholder="" v-model="patientinfo.padCheckInfoView.killip_level">
                        <Option v-for="item in killipArr" :value="item.code" :key="item.pk_defdoc">{{ item.name }}</Option>
                      </Select>
                    </FormItem>
                  </li>
                </ul>
                <ul class="xindiantu clearfix">
                  <li  style="width:100%;">
                    <FormItem label="肌钙蛋白"  class="formItem borderRadius0">
                      <span>cTnI</span>
                      <Input @on-focus="scrollChange(4)" :readonly="util.disabledFlag" style="width:100px;" :maxlength="20" @on-blur="troponin($event,1)" v-model="patientinfo.padCheckInfoView.ctni_value" @on-keyup="checkChange($event,['ctni_value'])"></Input>
                      <span style="width:30px;">单位</span>
                      <Select class="selectBorderRightNone" placement="top" :disabled="util.disabledFlag" style="width:104px;text-align:left;display:inline-block;" placeholder="" v-model="patientinfo.padCheckInfoView.ctni_unit">
                        <Option v-for="item in ctnlUnitArr" :value="item.code" :key="item.pk_defdoc">{{ item.name }}</Option>
                      </Select><Select :disabled="util.disabledFlag" placement="top" style="width:102px;text-align:left;display:inline-block;" placeholder="" v-model="patientinfo.padCheckInfoView.ctni_status">
                        <Option v-for="item in ctnlStatusArr" :value="item.code" :key="item.pk_defdoc">{{ item.name }}</Option>
                      </Select>
                    </FormItem>
                  </li>
                  <li  style="width:100%;">
                    <FormItem label=""  class="formItem borderRadius0">
                      <span>cTnT</span>
                      <Input @on-focus="scrollChange(5)" :readonly="util.disabledFlag" style="width:100px;" :maxlength="20" @on-blur="troponin($event,2)" @on-keyup="checkChange($event,['ctnt_value'])" v-model="patientinfo.padCheckInfoView.ctnt_value"></Input>
                      <span style="width:30px;">单位</span>
                      <Select :disabled="util.disabledFlag" placement="top" class="selectBorderRightNone" style="width:104px;text-align:left;display:inline-block;" placeholder="" v-model="patientinfo.padCheckInfoView.ctnt_unit">
                        <Option v-for="item in ctnlUnitArr" :value="item.code" :key="item.pk_defdoc">{{ item.name }}</Option>
                      </Select><Select :disabled="util.disabledFlag" placement="top" style="width:102px;text-align:left;display:inline-block;" placeholder="" v-model="patientinfo.padCheckInfoView.ctnt_status">
                        <Option v-for="item in ctnlStatusArr" :value="item.code" :key="item.pk_defdoc">{{ item.name }}</Option>
                      </Select>
                    </FormItem>
                  </li>
                  <li  style="width:100%;">
                    <FormItem label="血清肌酐(cr)"  class="formItem borderRadius0">
                      <!--<span>(cr)</span>-->
                      <Input @on-focus="scrollChange(6)" :readonly="util.disabledFlag" style="width:154px;" :maxlength="20" @on-keyup="checkChange($event,['cr_value'])" v-model="patientinfo.padCheckInfoView.cr_value"></Input>
                      <span style="width:30px;">单位</span>
                      <Select :disabled="util.disabledFlag" placement="top" style="width:206px;text-align:left;display:inline-block;" placeholder="" v-model="patientinfo.padCheckInfoView.cr_unit">
                        <Option v-for="item in crUnitArr" :value="item.code" :key="item.pk_defdoc">{{ item.name }}</Option>
                      </Select>
                    </FormItem>
                  </li>
                </ul>
              </div>
            </div>
          </Form>
        </div>
      <!--采集心电图是否保存-->
      <modal-dalog ref="collectConfirms" :closeFlag="false" :isShow="false" :wid="cancelWid" @oktip-modal="collectConfirm" :dalogTitle="cancelTaskTitle">
        <collect-tips  ref="collectContent" slot="dalogslot"></collect-tips>
      </modal-dalog>
      <!--查看心电图列表-->
      <modal-dalog ref="viewConfirms"  :wid="cancelWid" @oktip-modal="viewConfirm" :dalogTitle="viewTaskTitle">
        <view-ecg-list  ref="viewList"  :patient="patientinfo.padPatInfoView" slot="dalogslot"></view-ecg-list>
      </modal-dalog>
    </div>
</template>
<script>
import Vue from "vue";
// <!--采集心电图是否保存-->
Vue.component("collect-tips", {
  template:
  '<div style="font-size:14px;color:#333;width:100%;height:76px;padding:10px;margin:auto;overflow: hidden">' +
    '<p style="margin-bottom:10px;">采集心电图后保存是否成功？</p>'+
    '<RadioGroup v-model="isSaveSuccess">' +
    '        <Radio label="0">是</Radio>' +
    '        <Radio label="1">否</Radio>' +
    '</RadioGroup>' +
  '</div>',
  data() {
    return {
      isSaveSuccess:"0",
    };
  },
});
import viewEcgList from './viewEcgList';
export default {
  name: "",
  components:{viewEcgList},
  data() {
    return {
      src:"",
      pk_pat_ecg_ts:"",
      pk_pat_ecg:"",
      viewTaskTitle:"心电图列表",
      cancelTaskTitle:"采集心电图！",
      cancelWid:"600",
      checkRule:{
        ecg_conclusion:[
          {required: false,validator: this.validate.heartResult,message: '(超出心电图诊断结论最大长度100字)', trigger: 'blur' },
          {required: false,validator: this.validate.heartResult,message: '(超出心电图诊断结论最大长度100字)', trigger: 'submit' }
        ],
        respiration_rate:[
          {required: false,validator: this.validate.huxiRule,message: '(呼吸值必须为整数且大于0)', trigger: 'blur' },
          {required: false,validator: this.validate.huxiRule,message: '(呼吸值必须为整数且大于0)', trigger: 'submit' }
        ],
        pulse_rate:[
          {required: false,validator: this.validate.huxiRule,message: '(脉搏值必须为整数且大于0)', trigger: 'blur' },
          {required: false,validator: this.validate.huxiRule,message: '(脉搏值必须为整数且大于0)', trigger: 'submit' }
        ],
        heart_rate:[
          {required: false,validator: this.validate.huxiRule,message: '(心率值必须为整数且大于0)', trigger: 'blur' },
          {required: false,validator: this.validate.huxiRule,message: '(心率值必须为整数且大于0)', trigger: 'submit' }
        ],
      },
      transArr:[],//传输方式
      killipArr:[],//killip分级
      ctnlUnitArr:[],//ctnl单位
      ctnlStatusArr:[],//ctnl状态
      crUnitArr:[],//cr单位
      deviceCodeArr:[],//设备编号
      awareArr:[],
      ecg:false,
    };
  },
  created() {
    this.dictionary()
  },
  mounted() {},
  methods: {
    //字典接口
    dictionary(){
      var pThis=this;
      //传输方式--killip分级--ctni--ctnl单位--ctnl状态--意识
      var codes={code:["IS_REMOTE_ECGTRAN","KILLIPES","CRS","CT_UNIT","CT_STATUS","BOX_CODE","CONSCIOUSNESS_TYPE"]};
      pThis.$http.post(pThis.host('defdoc/getDefdocViewByCodes'),codes)
        .then(function (res) {
          let datajson = res.data;
          let backjson={"state":datajson.state,"msg":datajson.msg};
          pThis.backLogin(backjson);
          if(datajson.state=='1'){
            var data=datajson.results[0];
            for(var key in data){
              if(key=='IS_REMOTE_ECGTRAN'){//传输方式
                pThis.transArr=data[key];
              }
              if(key=='KILLIPES'){//killip分级
                pThis.killipArr=data[key];
              }
              if(key=='CRS'){//ctnl单位
                pThis.crUnitArr=data[key];
              }
              if(key=='CT_UNIT'){//ctnl状态
                pThis.ctnlUnitArr=data[key];
              }
              if(key=='CT_STATUS'){//ctnl状态
                pThis.ctnlStatusArr=data[key];
              }
              if(key=='BOX_CODE'){//ctnl状态
                pThis.deviceCodeArr=data[key];
              }
              if(key=='CONSCIOUSNESS_TYPE'){
                pThis.awareArr=data[key];
              }
            }
          }
        }).catch(function (error) {
        console.log(error)
      });
    },
    //页面tab切换
    versionNavChange(ev,index){
      $(".versionNav li h3").removeClass('modelTitleActive');
      $(ev.target).addClass('modelTitleActive');
      $('.changeContent>ul').css('display','none');
      $('.changeContent>ul').eq(index).css('display','block')
    },
    //采集心电图--按钮
    collectEcg(){
      var pThis=this;
      if(pThis.util.disabledFlag){
        pThis.$toast.center('warn.png','回到首页，选择用户?');
        return;
      }
      pThis.$http.post(pThis.host("patecg/savePatecg"),{pk_pat_info:pThis.patientinfo.padPatInfoView.pk_pat_info})
        .then(function (response) {
          var datajson = response.data;
          let backjson={"state":datajson.state,"msg":datajson.msg};
          pThis.backLogin(backjson);
          if (datajson.state == "1") {
            pThis.pk_pat_ecg=datajson.results[0].pk_pat_ecg;
            pThis.pk_pat_ecg_ts=datajson.results[0].ts;
            pThis.src="scheme1://host1:8080/path1?seriesInstanceUID="+datajson.results[0].pk_pat_ecg;
            pThis.ecg=true;
            setTimeout(function () {
              pThis.$refs.collectConfirms.openmodal();
            },5000);

          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    //查看心电图
    viewEcg(){
      this.$refs.viewConfirms.openmodal();
      this.$refs.viewList.init();
      // window.open("scheme2://host2:8080/path2?query1=2");
    },
    jumpMethod(){
      window.open(this.src)
    },
    //采集心电图--弹框--是否保存成功---确定按钮
    collectConfirm(){
      var pThis=this;
      pThis.ecg=false;
      // console.log(pThis.$refs.collectContent.isSaveSuccess)
      if(pThis.$refs.collectContent.isSaveSuccess=="1"){
        pThis.$http.post(pThis.host("patecg/deletePatecg"),{pk_pat_ecg:pThis.pk_pat_ecg,ts:pThis.pk_pat_ecg_ts})
          .then(function (response) {
            let datajson = response.data;
            let backjson={"state":datajson.state,"msg":datajson.msg};
            pThis.backLogin(backjson);
            if (datajson.state == "1") {
              pThis.$toast.center('success.png','删除成功');
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      }

    },
    //查看心电图--弹框
    //viewConfirm
    viewConfirm(){
      // alert('22')
    },
    //微信分享
    weixinShare(){},
    dateChange(ev,json,num){
      var time=this.dateTime(ev,num);
      Vue.set(this[json[0]][json[1]],json[2],time)
    },
    //dateTime
    dateTime(date,length){
      if(length==10){
        if(typeof date =='object'){
          var years=date.getFullYear();
          var months=date.getMonth()+1 <=9 ?"0"+(date.getMonth()+1):date.getMonth()+1;
          var days=date.getDate() <=9 ?"0"+date.getDate():date.getDate();
          return years+"-"+months+"-"+days;
        }
      }else{
        if(typeof date =='object'){
          var year=date.getFullYear();
          var month=date.getMonth()+1 <=9 ?"0"+(date.getMonth()+1):date.getMonth()+1;
          var day=date.getDate() <=9 ?"0"+date.getDate():date.getDate();
          var hours=date.getHours() <=9 ?"0"+date.getHours():date.getHours();
          var mins=date.getMinutes() <=9 ?"0"+date.getMinutes():date.getMinutes();
          var seds=date.getSeconds() <=9 ?"0"+date.getSeconds():date.getSeconds();
          return year+"-"+month+"-"+day+" "+hours+":"+mins+":"+seds;
        }
      }
      return date
    },
    //辅助检查规则
    checkChange(ev,json,num){
      var pThis=this;
      if (ev.keyCode == 37 || ev.keyCode == 39) {
        return;
      }else {
        if(!num){
          num=3
        }
        ev.target.value = ev.target.value.replace(/[\u4e00-\u9fa5]|[a-zA-Z]/g, '');
        pThis.patientinfo.padCheckInfoView[json[0]] = ev.target.value;
      }
    },
    //血压规则
    bloodRule(ev,json,num){
      var pThis=this;
      if (ev.keyCode == 37 || ev.keyCode == 39) {
        return;
      }else {
        if(!num){num=3}
        ev.target.value=ev.target.value.substr(0,num);
        ev.target.value = ev.target.value.replace(/[^\d]/g, '');
        pThis.patientinfo.padCheckInfoView[json[0]] = ev.target.value;
      }
    },
    //肌钙蛋白---时间
    troponin(ev,flag){
      var pThis=this;
        pThis.$http.post(pThis.host("public/systemTime"),{format:"yyyy-MM-dd HH:mm"})
          .then(function (response) {
            let datajson = response.data;
            if (datajson.state == "1") {
              if(flag=="1"){
                if(pThis.patientinfo.padCheckInfoView.ctni_value && !pThis.patientinfo.padCheckInfoView.ctnt_value){
                  // pThis.patientinfo.padTimeView.report_time=datajson.results[0].systemTime;
                  Vue.set(pThis.patientinfo.padTimeView,'report_time',datajson.results[0].systemTime)
                }
              }
              if(flag=="2"){
                if(!pThis.patientinfo.padCheckInfoView.ctni_value && pThis.patientinfo.padCheckInfoView.ctnt_value){
                  // pThis.patientinfo.padTimeView.report_time=datajson.results[0].systemTime;
                  Vue.set(pThis.patientinfo.padTimeView,'report_time',datajson.results[0].systemTime)
                }
              }
            }
          }).catch(function (error) {
          console.log(error);
        });
      },
    //获取焦点事件
    scrollChange(ev){
      var div = document.getElementById('versionInfo');
      if (div.scrollHeight) {
//        var height = div.scrollHeight;
        setTimeout(function () {
          $("#versionInfo").scrollTop(ev*40)
        }, 500)
      }
    },
    //血压的验证
    bloodBlur(blow,heigh){
      blow == null ? blow = "" : "";
      heigh == null ? heigh = "" : "";
      //高压大于低压
      if (parseInt(blow)>parseInt(heigh)) {
        this.util.isBloodFlag = false;
        this.util.bloodMessage="舒张压低于收缩压，比值不对!";
        this.$toast.center('warn.png','舒张压低于收缩压，比值不对!')
      } else if (!heigh && blow) {
        this.util.isBloodFlag = false;
        this.util.bloodMessage="舒张压未填写!";
        this.$toast.center('warn.png','舒张压未填写!')
      } else if (heigh && !blow) {
        this.util.bloodMessage="收缩压未填写!";
        this.$toast.center('warn.png','收缩压未填写!');
        this.util.isBloodFlag = false
      } else {
        this.util.isBloodFlag = true
      }
    },
  },
};
</script>

<style lang="scss" scoped>
  /*清除浮动*/
.clearfix:after{clear:both;display:block;content:"";}
  //页面所有字体设为===微软雅黑
.versionInfo{font-family: "微软雅黑";height:100%;overflow-y: auto;background:#fff;}
  //页面按钮样式设为===微软雅黑
.button{margin-left:10px;height:34px;line-height:34px;width:110px;font-size:14px;padding:0;}
.nativeInput{
  font-size: 14px;
  color: #333;height:36px;line-height:36px;
  border: 1px solid #c5ccdf;
  display: inline-block;
  width: 100%;
  padding: 4px 7px;
  background-color: #fff;
  background-image: none;
  position: relative;
  cursor: text;
  vertical-align: middle;
}
//页面tab切换样式
.xindiantu{
  padding-top:20px;font-size:14px;
  >li{
    width:50%;float:left;vertical-align: middle;
    label{margin-left:20px;display:inline-block;width:140px;text-align:right;vertical-align: middle;}
    span{vertical-align: middle;display:inline-block;width:50px;text-align:right;}
    .formItem{
      position:relative;
    }
  }
}
//页面底部按钮样式
.versionBtn{
  background:#fff;padding:20px 0;width:400px;margin:0 auto;
}
//设备信息---样式
.equipment{
  padding:20px 0;
  >li{
    float:left;height:34px;line-height:34px;width:50%;font-size:14px;
    label{display:inline-block;width:128px;text-align:right;margin-right:6px;}
  }
}
//页面标题
.modelTitle{
  height:45px;line-height:45px;font-weight:bold;color:#333;font-size:16px; border-bottom:1px solid #e8eaf1;
}
//tab切换---激活样式
.modelTitleActive{
  /*color:#173f99;*/
  color:rgb(23, 63, 153);
}
.versionNav{
  background:#fff;height:46px;line-height:46px; border-bottom:1px solid #e8eaf1;
  position:relative;
  .heartShare{
    position:absolute;top:0;right:14px;
    li{float:left;padding:0 16px 0 24px;font-size:14px;color:#333;}
    li:first-child{background:url("../../../static/img/collectheart.png")no-repeat left 12px;height:46px;line-height:46px; }
    li:nth-child(2){background:url("../../../static/img/seeheart.png")no-repeat left 12px;height:46px;line-height:46px; }
    li:nth-child(3){background:url("../../../static/img/weixinshare.png")no-repeat left 12px;height:46px;line-height:46px; }
  }
  >li{float:left;background:#fff;cursor:pointer;padding:0 20px;}
}
.changeContent{
  background:#fff;
  >.xindiantu{display:none;}
  .ulOne{display:block;}
}
</style>





